.main{
    height: 500px;
    border: 2px solid black;
    border-radius: 20px;
    background-color: rgb(0, 228, 228);
    display:grid;
    justify-content: center;
}
form{
    display:grid;
    height: 200px;
    justify-content: center;
}
input{
    margin: 3px;
    width: 200px;
    border: 2px black solid;
    background: white;
    transition: 0.5s background,0.5s transform;
}
button{
    background-color: rgb(238, 255, 0);
    margin-left: 5px;
    height: 30px;
    width: 200px;
    transition: 0.5s background,0.5s transform;
}
.submit:hover{
    cursor:pointer;
    transform: translateX(2px);
    background: rgb(235, 234, 234);
}
button:hover{
    transform: translateX(2px);
    cursor:pointer;
    background-color: rgb(255, 238, 0);
}
.clear{
    background: rgb(175, 3, 3);
}
.clear:hover{
    background-color: red;
}